  mid += Math.imul(al7, bh9);
  mid += Math.imul(ah7, bl9);
  hi += Math.imul(ah7, bh9);
  var w16 = c + lo + ((mid & 0x1fff) << 13);
  c = hi + (mid >>> 13) + (w16 >>> 26);
  w16 &= 0x3ffffff;
