  var w17 = c + lo + ((mid & 0x1fff) << 13);
  c = hi + (mid >>> 13) + (w17 >>> 26);
  w17 &= 0x3ffffff;
  /* k = 18 */

  lo = Math.imul(al9, bl9);
  mid = Math.imul(al9, bh9);
  mid += Math.imul(ah9, bl9);
  hi = Math.imul(ah9, bh9);
  var w18 = c + lo + ((mid & 0x1fff) << 13);
  c = hi + (mid >>> 13) + (w18 >>> 26);
  w18 &= 0x3ffffff;
  o[0] = w0;
  o[1] = w1;
  o[2] = w2;
  o[3] = w3;
